import './Home.css';
import Value1 from "../assets/Value1.svg";
import Value2 from "../assets/Value2.svg";
import Value3 from "../assets/Value3.svg";
import Value4 from "../assets/Value4.svg";
import Value5 from "../assets/Value5.svg";
import leewonsuk from "../assets/leewonsuk.svg";
import kimhongki from "../assets/kimhongki.svg";

function Home() {
    return (
      <div className="app-container">
        <div className="value-container">
          <img className="value1" src={Value1} alt="Trust" />
          <img className="value2" src={Value2} alt="Respect" />
          <img className="value3" src={Value3} alt="Accountability" />
          <img className="value4" src={Value4} alt="Collaboration" />
          <img className="value5" src={Value5} alt="Innovation" />
        </div>
        <img className="leewonsuk" src={leewonsuk} alt="leewonsuk" />
        <img className="kimhongki" src={kimhongki} alt="kimhongki" />
      </div>
    );
  }
  
  export default Home;