import { Link } from "react-router-dom";
import React from 'react';
import "./Header.css";
import KREN from "../assets/KREN_logo.svg";
const Header = () => {

  return (
    <div className="header-container">
      <div className="inner-container">
        <img className="logo" src={KREN} alt="KREN"/>
        <div className="letter-frame">
          <div className="movie"><Link to="/" style={{ textDecoration: "none" , color:"white"}}></Link></div>
          <div className="service"><Link to="/service" style={{ textDecoration: "none" , color:"white"}}></Link></div>
          <div className="IT"><Link to="/it" style={{ textDecoration: "none" , color:"white"}}>IT</Link></div>
          <div className="conference"><Link to="/conference" style={{ textDecoration: "none" , color:"white"}}></Link></div>
        </div>
        <div className="login-button">
          
        </div>

      </div>
    </div>
  );
};
export default Header;